.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ul {
  margin: 20px auto;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  list-style-type: none;
  font-size: 16px;
  font-weight: 700;
  text-align: left;
}

iframe {
  margin: 0 auto;
}

.span {
  color: #4bd0a0;
}